.parent{
  width:90%;
  margin:auto; 
  font-size:15px;
  line-height: 1.5;
  padding:0;
}


.bar{  /*Menu Box */
    background:  #ffffff;
    margin:0;
    
    color: #2874A6;
    padding:5px 25px;
    min-height:5px;
    border-bottom: 	#2874A6 1px solid;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

header a{
  color: #17202A;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;
}


.branding{
  margin:0;
  padding:0;
}

.branding h1{
  margin:0;
}

