.foot{
    background-color: #fff; 
    padding: 10px;
    
    margin-top: 60px; /*Change this for inserting app */
    border-top: #2874A6 30px solid;
    width:100%;
    border-bottom: #2874A6 5px solid;
    z-index:2;
  }


  .footerlinks{
    display: block;
    margin: 0 auto;
  }
  .footerlink{
    margin: 20px auto;
    user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
  }
  .footerlink ol{
    width:50%;
    display: -ms-flexbox;
    display: flex;
    padding: 0;
    margin: auto;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    list-style-type: none;
  }
  
  
  .footercopyright{
    text-align: center;
  }
  .footercopyright span{
    color: #666666;
    font-size: 14px;
  }

  

.footerlink a{
    text-decoration: none;
    text-align: center;
    display:inline;
    text-transform: uppercase;
    color: #333333;
    font-size: 18px;
    font-weight: bolder;
    padding:0px 0px 0px 0px;
}

.footerlink a:hover{
    color: #AD1453;
}



