.content {
    text-align: center;
    width: 90%;
    margin: 0 auto;
    
}


.content .form {
    position:absolute;
    top: 60px;
    right:0px;
    width: 23%;
    
}

.content .in{
    display:inline-block;
    width:90%;
    margin:5px;
    border-radius: 4px;
    border: 1px solid #4e5766;
    -webkit-box-shadow: 1px 0.5px #888888;
            box-shadow: 1px 0.5px #888888;
    font-size: 18px;
}

.content label {
    float:left;
    font-size:14px;
    text-align: left;
    margin-left: 20%;
    margin-top: 1px;
}

.content .drop {
    display:inline-block;
    
    margin:6px;
    border-radius: 4px;
    border: 1px solid #4e5766;
    -webkit-box-shadow: 1px 0.5px #888888;
            box-shadow: 1px 0.5px #888888;
    font-size: 14px;
}

.content .button {
    display:inline-block;
    border-radius: 4px;
    width:20%;
    margin:5px;
    border: 1px solid #4e5766;
    -webkit-box-shadow: 1px 0.5px #888888;
            box-shadow: 1px 0.5px #888888;
    font-size: 14px;
}

.tree{
    height:700px;
    margin-top: 70px;
}


span {
	font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
	color: #F4F4F4;
	text-shadow: 0 1px 4px black;
	float: right;
}



