.modal{
    position:fixed;
    z-index:2;
    background-color: white;
    width:85%;
    border: 1px solid black;
    -webkit-box-shadow: 4px 4px 3px #1453ad;
            box-shadow: 4px 4px 3px #1453ad;
    border-radius: 5%;
    padding:16px;
    top:5%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    height:90%;
    overflow: scroll;
}

.modal h1{
    border-bottom: #2874A6 5px solid;
}

.modal div{
    margin:auto;
    -ms-touch-action : none;
        touch-action : none;

}


